/* ==========================================================================
   TYPOGRAPHY STYLES
   ========================================================================== */
/*
    * Basic typography for copy text
    */
body {
  font-family: "Didact Gothic", "Futura Medium", "Century Gothic", "Apple Gothic", "Avant Garde", sans-serif;
  font-size: 16px;
}
body h1 {
  color: rgba(55, 55, 55, 0.8);
  font-size: 3em;
  margin-bottom: 0em;
  text-transform: capitalize;
  font-family: "Patua One", "Roboto Slab", serif;
}
body h2 {
  font-size: 3.5rem;
  line-height: 1.45;
  color: #444;
}
body h3 {
  font-size: 3.5rem;
  line-height: 1.45;
}
body h4 {
  font-family: "Patua One", "Roboto Slab", serif;
  margin-top: 0.3em;
  margin-bottom: 0.8em;
}
body h5 {
  color: rgba(55, 55, 55, 0.8);
  font-size: 1.5em;
  margin-top: 0em;
  text-transform: lowercase;
}

/*
 * Basic link styles
 */
a {
  font-weight: bold;
  text-decoration: underline;
}
a:hover, a:active, a:focus {
  text-decoration: none;
}

/**
 * Clear inner floats
 */
.clearfix::after {
  clear: both;
  content: "";
  display: table;
}

/**
 * Hide text while making it readable for screen readers
 * 1. Needed in WebKit-based browsers because of an implementation bug;
 *    See: https://code.google.com/p/chromium/issues/detail?id=457146
 */
.hide-text {
  overflow: hidden;
  padding: 0;
  /* 1 */
  text-indent: 101%;
  white-space: nowrap;
}

/**
 * Hide element while making it readable for screen readers
