 * https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css#L119-L133
 */
.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

/*
 * Helper class to center texts
 */
.centered {
  text-align: center;
}

/*
 * Class for hiding elements
 */
.hidden {
  display: none;
}

/* These are hidden until user has logged in */
.on-map-view {
  visibility: hidden;
}

.full-opacity {
  opacity: 0.9;
}

.no-select {
  user-select: none;
}

/* CHANGE BUTTON
   ========================================================================== */
.change-button {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin-bottom: 5px;
  background: #acacac;
  opacity: 0.5;
  cursor: pointer;
  overflow: hidden;
  display: block;
}
.change-button:hover {
  overflow: visible;
  opacity: 1;
}
.change-button:active {
  overflow: visible;
  opacity: 0.3;
}
.change-button img {
  display: block;
  margin: 0 auto;
  padding: 3px 0;
}
.change-button p {
  width: 200px;
  margin: -30px -210px;
  text-align: right;
  font-weight: 700;
}

/*
 * Specific change buttons
 */
#clear-cache-button img {
  padding-top: 5px;
}

.button-group {
  display: flex;
  align-items: center;
  gap: 5px;
}
.button-group .change-button:hover + .button-group__label {
  display: block;
}
.button-group__label {
  display: none;
  order: -1;
  font-weight: 700;
  margin: -6px 5px 0;
}

/* GO-BUTTON (SPLASH)
   ========================================================================== */
.gobutton {
  width: 130px;
  height: 40px;
  overflow: hidden;
  margin: 1em auto 0 auto;
  padding: 3px 6px 6px 6px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  background: #ffffff;
  background: linear-gradient(to bottom, #ffffff 0%, #ffc3c3 100%);
  border: 2px solid grey;
}
.gobutton:hover {
  background: white;
  background: #ffc3c3;
  background: linear-gradient(to bottom, #ffffff 0%, #ffeaea 100%);
}
.gobutton:active {
  background: #ffc3c3;
  background: linear-gradient(to bottom, #ffc3c3 0%, #ffffff 100%);
}
.gobutton:active input {
  margin-top: 1px;
}
.gobutton input {
  background: url(../img/gobutton.png) no-repeat;
  background-size: 90%;
  background-position: center top;
  cursor: pointer;
  border: none;
  width: inherit;
  height: inherit;
}

/* ==========================================================================
   THE LOADER DIV
   ========================================================================== */
.loader {
  position: absolute;
  z-index: 1;
  top: 50px;
  padding: 25px 40px;
  left: 80%;
  background: rgba(45, 45, 45, 0.5);
  border-radius: 40px;
  color: white;
  text-align: center;
  pointer-events: none;
  opacity: 0;
  /* Fade in later */
}
.loader a {
  color: white !important;
}

body.black-theme .loader {
  background: rgba(80, 80, 80, 0.4);
}

/* Animated bubbles
   ========================================================================== */
.bubblingG {
  text-align: center;
  width: 107px;
  height: 67px;
}
.bubblingG span {
  display: inline-block;
  vertical-align: middle;
  width: 13px;
  height: 13px;
  margin: 33px auto;
  background: white;
  -moz-border-radius: 67px;
  -moz-animation: bubblingG 1.3s infinite alternate;
  -webkit-border-radius: 67px;
  -webkit-animation: bubblingG 1.3s infinite alternate;
  -ms-border-radius: 67px;
  -ms-animation: bubblingG 1.3s infinite alternate;
  -o-border-radius: 67px;
  -o-animation: bubblingG 1.3s infinite alternate;
  border-radius: 67px;
  animation: bubblingG 1.3s infinite alternate;
}

/* Animation styles
   ========================================================================== */
#bubblingG_1 {
  -moz-animation-delay: 0s;
  -webkit-animation-delay: 0s;
  -ms-animation-delay: 0s;
  -o-animation-delay: 0s;
  animation-delay: 0s;
}

#bubblingG_2 {
  -moz-animation-delay: 0.39s;
  -webkit-animation-delay: 0.39s;
  -ms-animation-delay: 0.39s;
  -o-animation-delay: 0.39s;
  animation-delay: 0.39s;
}

#bubblingG_3 {
  -moz-animation-delay: 0.78s;
  -webkit-animation-delay: 0.78s;
  -ms-animation-delay: 0.78s;
  -o-animation-delay: 0.78s;
  animation-delay: 0.78s;
}

@-moz-keyframes bubblingG {
  0% {
    width: 13px;
    height: 13px;
    background-color: white;
    -moz-transform: translateY(0);
  }
  100% {
    width: 32px;
    height: 32px;
    background-color: #FFFFFF;
    -moz-transform: translateY(-28px);
  }
}
@-webkit-keyframes bubblingG {
  0% {
    width: 13px;
    height: 13px;
    background-color: white;
    -webkit-transform: translateY(0);
  }
  100% {
    width: 32px;
    height: 32px;
    background-color: #FFFFFF;
    -webkit-transform: translateY(-28px);
  }
}
@-ms-keyframes bubblingG {
  0% {
    width: 13px;
    height: 13px;
    background-color: white;
    -ms-transform: translateY(0);
  }
  100% {
    width: 32px;
    height: 32px;
    background-color: #FFFFFF;
    -ms-transform: translateY(-28px);
  }
}
@-o-keyframes bubblingG {
  0% {
    width: 13px;
    height: 13px;
    background-color: white;
    -o-transform: translateY(0);
  }
  100% {
    width: 32px;
    height: 32px;
    background-color: #FFFFFF;
    -o-transform: translateY(-28px);
  }
}
@keyframes bubblingG {
  0% {
    width: 13px;
    height: 13px;
    background-color: white;
    transform: translateY(0);
  }
  100% {
    width: 32px;
    height: 32px;
    background-color: #FFFFFF;
    transform: translateY(-28px);
  }
}
div.tooltip {
  font-size: 0.8em;
  background: #fff;
  padding: 0.5em;
  text-shadow: #f5f5f5 0 1px 0;
  border-radius: 2px;
  box-shadow: 0px 0px 2px 0px #a6a6a6;
  opacity: 0.9;
  position: absolute;
  z-index: 1;
}

/* ==========================================================================
   Map container
   ========================================================================== */
#map-container {
  height: 100%;
  overflow: hidden;
}

/* ==========================================================================
   Countries (SVG)
   ========================================================================== */
svg:active {
  cursor: move;
}

.country {
  cursor: pointer;
}
.country:hover {
  stroke: #FFF7EE;
  stroke-width: 1.2px;
}
.country.highlighted, .country.highlighted:hover {
  stroke: #444;
  stroke-width: 2.5px;
}

/* ==========================================================================
   Map legend
   ========================================================================== */
.legend {
  font-size: 14px;
  font-family: "Patua One", "Roboto Slab", serif;
}
.legend rect {
  stroke: #fff;
}
.legend text {
  font-family: "Didact Gothic", "Futura Medium", "Century Gothic", "Apple Gothic", "Avant Garde", sans-serif;
}
.legend#filter {
  cursor: pointer;
  text-decoration: underline;
  font-style: italic;
}

/* ==========================================================================
   Controls container
   ========================================================================== */
#controls {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 80px;
  padding: 20px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

/* ==========================================================================
   Progress bar
   ========================================================================== */
#countryCount {
  position: absolute;
  top: 165px;
  width: 10px;
  bottom: 20px;
  right: 32px;
  box-shadow: inset 1px 1px 3px 0px rgba(0, 0, 0, 0.39);
  border: 0.1em solid;
  z-index: 0;
  border-radius: 50px;
}

#progress-bar {
  float: left;
  width: 100%;
  height: 0;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  position: absolute;
  bottom: 0;
  background-color: #ae017e;
  transition: height 0.6s ease;
  box-shadow: inset 1px 1px 3px 0px rgba(0, 0, 0, 0.39);
}

#progress-text {
  float: right;
  margin-right: 22px;
  margin-top: -20px;
  width: auto;
  line-height: 12px;
  border-radius: 8px;
  padding: 5px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(175, 175, 175, 0.5);
}
#progress-text:after, #progress-text:before {
  left: -13px;
  top: 0px;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
#progress-text:after {
  border-color: rgba(255, 255, 255, 0);
  border-left-color: rgba(255, 255, 255, 0.5);
  border-width: 7px;
  margin-top: -7px;
}
#progress-text:before {
  border-color: rgba(175, 175, 175, 0);
  border-left-color: rgba(175, 175, 175, 0.5);
  border-width: 9px;
  margin-top: -9px;
}

/* ==========================================================================
   Friendbox
