  font-size: 1em;
  transition: 0.5s opacity;
  pointer-events: none;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-size: 14px;
  transition: 0.3s all ease-out;
  visibility: hidden;
}
.no-countries:hover {
  opacity: 1;
  height: 200px;
}
.no-countries ul {
  padding: 0;
  margin: 0;
  line-height: 1.1em;
}
.no-countries li {
  list-style: none;
  padding-bottom: 0.5em;
}
.no-countries__title {
  margin-bottom: 0.3em;
  font-family: "Patua One", "Roboto Slab", serif;
}
.no-countries__link {
  font-weight: normal;
}
.no-countries__content {
  padding-right: 0.5em;
  overflow: auto;
}
.no-countries__content p {
  margin: 0 0 0.5em;
}
.no-countries__info {
  font-size: 0.9em;
  line-height: 1.1em;
  font-weight: bold;
  margin: 0.8em 0 0.7em !important;
  border-top: 1px solid #eee;
  padding-top: 1em;
}

.artist-test {
  display: none;
  position: absolute;
  top: 20px;
  left: 20px;
}

/* ==========================================================================
   In this file we store styles for the splash screen
   ========================================================================== */
/* Container
   ========================================================================== */
#welcome-container {
  position: absolute;
  width: 100%;
  display: flex;
  height: 100%;
  background: rgba(255, 200, 200, 0.5);
  visibility: hidden;
}

#splashTextContainer {
  overflow-y: auto;
  max-height: 400px;
  padding: 0px 13px 0px 13px;
}

/* Welcome text
   ========================================================================== */
.welcome-message {
  margin: 60px auto auto auto;
  padding: 20px 30px 20px 30px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 30px;
  text-align: center;
  width: 330px;
  border: 5px solid white;
  vertical-align: top;
}

.info {
  text-align: left;
  margin-top: 5px;
  margin-bottom: 10px;
}

.infoPoint {
  text-align: left;
  margin: 0.3em 0 0 0;
  font-size: 0.9em;
}

#made-by {
  font-size: 13px;
  margin: 20px 0 0 0;
  opacity: 0.4;
}

.faq-link {
  font-size: 15px;
  margin: 20px 0 0 0;
  opacity: 1;
  cursor: pointer;
  text-align: center;
}

.donate {
  position: relative;
  padding: 1em 0;
  margin: 1em 0;
}
.donate__title {
  font-size: 0.9em;
  opacity: 0.7;
  margin-top: 0;
}
.donate__list {
  display: flex;
  align-items: center;
  justify-content: center;
}
.donate__list a {
  margin: 0 0.4em;
}
.donate:before, .donate:after {
  content: "";
  position: absolute;
  width: 263px;
  height: 1px;
  background: #c4c4c4;
  transform: translate(-50%);
  opacity: 0.5;
}
.donate:before {
  top: 0;
}
.donate:after {
  bottom: 0;
}

.flattr-logo {
  height: 1em;
  text-decoration: none;
}

/* Image
   ========================================================================== */
.logoImageWelcome {
  margin-bottom: 4px;
}

#logo {
  position: absolute;
  bottom: 35px;
  right: 20px;
  /*opacity: 0.5;*/
  pointer-events: none;
}

/* Form
   ========================================================================== */
#start-form {
  margin-top: 0.25em;
}

.usernameinput {
  height: 30px;
  width: 170px;
  color: #222;
  font-weight: bold;
  background: rgba(255, 255, 255, 0.8);
  margin-top: 0.5em;
  /*text-align: center;*/
}

/*
 * In this file we store styles for the zoomed in map view
 */
/* ==========================================================================
   Main container
   ========================================================================== */
.infoContainer {
  margin-left: 7px;
  margin-right: 7px;
  width: 99%;
  height: 150px;
  position: absolute;
  top: 7.5%;
  border-top: 1px solid black;
  z-index: 4;
  opacity: 0;
}

.cnameDiv {
  display: block;
  float: right;
  margin-right: 5%;
  text-align: right;
  pointer-events: none;
}

/* ==========================================================================
   Artist info
   ========================================================================== */
.artistContainer {
  float: right;
  margin-right: 6%;
  width: 592px;
  display: block;
  background: inherit;
  position: relative;
}
.artistContainer h4 {
  font-size: 1.15em;
  margin-top: 1em;
  font-weight: bold;
}
.artistContainer p {
  font-size: 0.8em;
}

.detailsDiv {
  display: table;
  margin-left: 16px;
  padding: 0 30px;
  padding: 0;
  opacity: 0.9;
  vertical-align: top;
  position: relative;
}
.detailsDiv h5 {
  color: rgba(55, 55, 55, 0.5);
  font-size: 0.9em;
  margin-top: 0;
  text-transform: lowercase;
}

.topartists-desc {
  font-size: 1em;
  margin-top: 1em;
  margin-bottom: 0.8em;
  font-weight: bold;
}

.demonym {
  font-size: 1.15em;
  font-weight: bold;
  font-family: "Patua One", "Roboto Slab", serif;
}

.fa.artist-control {
  position: absolute;
  font-size: 40px;
  top: 85px;
  color: rgba(90, 90, 90, 0.8);
  cursor: pointer;
  transition: 0.1s;
}
.fa.artist-control.right {
  right: -8px;
}
.fa.artist-control.left {
  left: -8px;
}
.fa.artist-control.disabled {
  cursor: auto;
  opacity: 0.5;
}
.fa.artist-control.disabled:hover {
  color: rgba(90, 90, 90, 0.8);
  transform: none;
}
.fa.artist-control:hover {
  transform: scale(2.5);
  color: pink;
}

.details-p {
  font-size: 0.8em;
  font-style: #000;
  margin-bottom: 4px;
}

.summaryText {
  display: inline-block;
  background: inherit;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid rgba(175, 175, 175, 0.5);
}
.summaryText p {
  margin-bottom: 0;
  margin-top: 0.3em;
}
.summaryText h4 {
  font-size: 1.1em;
  margin: 0;
}

/* ==========================================================================
   Artist images
   ========================================================================== */
.artist-div {
  float: left;
  text-align: center;
  width: 112px;
  padding-top: 7px;
  cursor: pointer;
  border-radius: 7px;
  box-sizing: border-box;
  transition: background 0.1s;
}
.artist-div .image-div {
  transition: border-radius 0.1s;
}
.artist-div a {
  text-decoration: none;
}

.image-div {
  height: 96px;
  width: 96px;
  background-position: center top;
  border: 2px solid #444;
  border-radius: 2px;
  margin-left: auto;
  margin-right: auto;
  background-size: cover;
  text-align: center;
}

.overlayNo {
  opacity: 0.35;
  color: #fff;
  text-decoration: none;
  font-size: 5em;
  line-height: 86px;
}

.artist-div.highlight, .reco-div.highlight {
  background: rgba(175, 175, 175, 0.5);
}

.artist-div.lowlight .image-div {
  border-radius: 50%;
}

.artist-div.lowlight .image-div:hover {
  border-radius: 30%;
}

/* ==========================================================================
   Artist tags
   ========================================================================== */
.tagdiv {
  border: 1px solid lightgrey;
  color: #969696;
  display: inline-block;
  background: inherit;
  margin-top: 0.6em;
  margin-bottom: 0.3em;
  padding: 0.3em;
  margin-right: 0.2em;
  width: auto;
}
.tagdiv h4 {
  font-size: 0.8em;
  font-weight: normal;
}
.tagdiv .usertag {
  color: #636363;
  font-weight: bolder;
}

/* ==========================================================================
   Recommendations
   ========================================================================== */
.recoDiv {
  display: table;
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 16px;
  padding: 0 30px;
  padding: 0;
  opacity: 0.9;
  vertical-align: top;
  width: 560px;
}

.recLoadingDiv {
  width: 100%;
  margin-bottom: 1em;
}

/* ==========================================================================
   Close button
   ========================================================================== */
.close-button {
  width: 25px;
  height: 25px;
  display: block;
  text-transform: uppercase;
  color: #444;
  background: #fff;
  border: 0.1em solid #444;
  border-radius: 0.2em;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.1em;
  top: 0.1em;
  right: 0;
  position: absolute;
  cursor: pointer;
}
.close-button:hover {
  color: #fff;
  background: #aaa;
}
.close-button:active {
  color: #444;
  background: #fff;
}

/** FOR THEMES!! **/
